body {
	background-color: #fff;
	font-family: 'Raleway', sans-serif;
	color: $grey;
	position: relative;
	margin: 0;
	padding-bottom: 6rem;
	min-height: 100%;
}

.container {
	padding-top: 80px;
}

.footer {
	position: fixed;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 1rem;
	border-top: 2px solid #e8e8e8;
	text-align: center;
	display: flex;
	z-index: 5;
	background-color: #fff;
}

.nav-link {
	color: $blue-green;
	font-size: 1.2em;
}

.nav-link i {
	padding-right: 6px;
	font-size: 1.3em;
	font-weight: 100;
}

.add-on input {
	text-align: center;
	width: 100%;
}

.add-on .input-group-btn {
	position: absolute;
}

.add-on .btn-default {
	border-color: transparent !important;
}

.add-on .btn-default:hover {
	background-color: #fff;
	cursor: pointer;
}

.add-on .input-group-btn.left {
	top: 4px;
	border: 0;
	left: 0;
	z-index: 4;
}

.add-on .input-group-btn.left i {
	color: red;
}

.add-on .input-group-btn.right i {
	color: $blue-green;
}

.add-on .input-group-btn.right {
	top: 4px;
	border: 0;
	right: 35px !important;
	z-index: 4;
}
/* stop the glowing blue shadow */
.add-on .form-control:focus {
	border-color: #cccccc;
}

.form-control[disabled],
.form-control[readonly] {
	background-color: #fff;
}

.credit-proposals-wrapper {
	padding-top: 25px;
}

.credit-proposals-wrapper .panel .panel-header span {
	display: block;
	margin: 8px 0;
}

.credit-proposals-wrapper img {
	width: 13%;
}

.credit-proposals-wrapper .panel {
	border-radius: 18px;
}

.credit-proposals-wrapper .panel .panel-header {
	border-radius: 15px;
	color: #fff;
	text-align: center;
	background-color: #333;
	padding: 15px;
}

#credit-proposals ul {
	list-style-type: none;
}

.credit-proposals-wrapper .panel.active .panel-header {
	background-color: $blue-green;
}

.credit-proposals-wrapper .panel.panel-red .panel-header {
	background-color: $red-bg;
}

.credit-proposals-wrapper .panel.panel-green .panel-header {
	background-color: $green-bg;
}

.credit-proposals-wrapper .panel .panel-body .list-title {
	font-weight: 800;
	font-size: 16px;
}

.credit-proposals-wrapper .panel .panel-body .list-item {
	color: $blue-green;
	font-weight: 500;
}

#pre-autorizacion p {
	    margin: 32px 0;
}

.img-title {
	padding-top: 25px;
}

.folio-aut {
	padding-bottom: 25px;
}

.folio-aut img {
	padding-left: 10px;
}

.denegado .title-section {
	padding-bottom: 64px;
}

.dropzone {
	box-shadow: 0px 3px 4px #e8e8e8;
    border: 0;
    min-height: 100px;
    border-radius: 8px;
}
.dropzone .dz-message span i{
	position: absolute;
    top: 10px;
    right: 23px;
    font-size: 2em;
		color: $blue-green;
}


/* importamos responsive al final */
@import 'partials/responsive';
