/* tablet, landscape iPad, lo-res laptops ands desktops */
@media (min-width:801px) {
	.container {
		padding-top: 100px;
		    padding-left: 6em;
	}

	.footer {
		display: none;
	}

	body {
		background: repeating-linear-gradient( 45deg, #fcfbfb, #fff 2px, #fff 2px, #fcfbfb 3px );
	}

	.desktop-nav-links {
		display: block;
	}

	#nav-close {
		display: none;
	}

	.row {
		margin-right: 0;
		margin-left: 25px;
	}

	.navbar {
		background-color: #fcfbfb;
		box-shadow: 0 4px 2px -2px #ccc;
		border: 0;
	}

	.navbar-header {
		display: none;
	}

.center {
	text-align: center;
}
	.btn-form {
		color: white;
background-color: $blue-green;
padding: 2px 43px;
	}

	nav {
		box-shadow: 2px 0 2px -2px #ccc;
		width: 12em;
		left: -6em;
	}

	.desktop-nav-links {
		padding-left: 6em;
		/* padding-top: 1em; */
		margin: 10px 2px;
	}

	.desktop-nav-links input {}

	.desktop-nav-links .notifications img {
		float: right;
		height: 32px;
		padding-right: 2em;
	}

	.desktop-nav-links .search {
		padding-top: 1em;
	}

	.floating-icon {
		position: fixed;
		z-index: 2100;
	}

	span.open-menu i {
		color: $blue-green;
		right: -9px;
		position: absolute;
		padding: 5px;
		box-shadow: 0 2px 3px #e8e8e8;
		z-index: 1032;
		-moz-transform: rotate(90deg);
		-webkit-transform: rotate(90deg);
		-ms--transform: rotate(90deg);
		transform: rotate(90deg);
	}

	.icon-item {
		box-shadow: 0 2px 3px #e8e8e8;
		border-radius: 5px;
		padding: 15px 10px;
		margin: 0 auto;
		text-align: center;
		width: 70px;
		background: #fff;
	}

	.sidebar-header p,
	.sidebar-header-profilepic {
		display: none;
	}

	ul.nav.navbar-nav li.notification {}

	ul.nav.navbar-nav li.profile img {
		height: 64px;
		border-radius: 50%;
	}

	ul.nav.navbar-nav li.profile-name {
		margin: 1em;
		top: 5px;
		font-weight: 600;
	}

	.sidebar-header {
		text-align: right;
	}

	.sidebar-header-profilepic img {
		height: 50px;
		border-radius: 50%;
	}

	.sidebar-header img {
		height: 30px;
		padding-right: 15px;
	}

	.sidebar-header-logo {
		padding-top: 15px;
		padding-bottom: 30px;
	}

	.main-menu li {
		margin-left: 0;
    font-size: 14px;
    padding-right: 16px;
    text-align: center;
    flex: 1;
	}

	.main-menu {
		display: flex;
    flex-direction: column;
    height: 80%;
    justify-content: space-between;
	}

	.main-menu li a {
		display: none;
	}

	.nav-link {
		display: none;
	}

	span.icon {
		display: block;
		text-align: right;

	}

	.nav-expanded .sidebar-header {
		text-align: center;
	}

	.nav-expanded .main-menu li a {
		display: block;
	}

	.nav-expanded span.icon {
		text-align: center;

	}

	.nav-expanded span.icon img{
		padding: 0 !important;
	}

	body.nav-expanded .mobile-wrapper {
		display: none;
	}

	.title-section {
		font-size: 16px;
	}

  .scrollmenu {
    text-transform: none;
    display: flex;
  }
  .scrollmenu a {
    flex: 1;
    padding: 20px;
  }
  .scrollmenu a.active {
    color: $blue-green;
    border-bottom: 2px solid $blue-green;
  }
  .credit-promo {
    margin: 0 auto;
  }
  .swiper-slide {
  	width: 30%;
  }

	.panel {
    background-color: transparent;
	}

	.panel-default>.panel-heading+.panel-collapse>.panel-body {
	    border-top-color: transparent;
	}
	.panel-default, .panel-group .panel-heading+.panel-collapse>.list-group, .panel-group .panel-heading+.panel-collapse>.panel-body {

    border: 0;
	}

	.panel-default>.panel-heading {
    border: 1px solid $blue-green;
    text-align: center;
		    background-color: transparent;
	}

	.panel-title i {
		float: right;
    color: $blue-green;
	}
	.code-input {
		font-size: 48px;
	}
}
