.icon-item {
  box-shadow: 0px 2px 3px #e8e8e8;
      border-radius: 5px;
      padding: 15px 10px;
    margin: 0px -10px;
      text-align: center;
}

.icon-item img {
  height: 36px;
}

.icon-item.active {
  border: 1px solid $blue-green;
  box-shadow: none;
}

.credit-text{
  text-align: center;
margin-top: 5px;
}

.form-control, .form-control:focus {
  /* Remove First */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  -webkit-box-shadow: 0px 3px 4px #e8e8e8;
          box-shadow: 0px 3px 4px #e8e8e8;
    border: 0;
}

.form-group {
    margin-bottom: 15px;
    margin-top: 20px;
}

.footer .icon-menu {
  flex: 1;
}

.footer .icon-menu img {
  height: 36px;
}


svg.no  {
  -moz-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -ms--transform: rotate(180deg);
    transform: rotate(180deg);
}
svg.no path{
  fill: $red-bg !important;

}

label.icon-label {
    text-align: center;
}

label.icon-label span {
  padding-left: 20px;
}
