.title-section {
	font-size: 20px;
	font-weight: 700;
	text-align: center;
}

h2.credit-form-title {
	font-size: 16px;
	padding-top: 10px;
}

.form-group span {
	display: block;
	font-size: 11px;
	padding-left: 5px;
	line-height: 25px;
}

span.input-success {
	color: $blue-green;
}

h3.section-header {
	font-size: 20px;
	padding-bottom: 15px;
	border-bottom: 1px solid $green-bg;
}

.section-list-title {
	font-size: 12px;
	color: #2B3B46;
	line-height: 16px;
	text-transform: uppercase;
  margin: 10px 0;
}

.section-list-item {
	font-size: 18px;
	color: $blue-green;
	line-height: 24px;
}
