.navbar {
  background: $header-bg;
  border: 1px solid $header-bg;
  border-radius: 0;
}

.desktop-nav-links {
  display: none;
}

.notifications img{
  float: right;
height: 32px;
margin: 15px 15px;
}

.menuicon img{
  margin: 10px 15px;
  height: 40px;
}

.scrollmenu{
    background-color: #fff;
    overflow: auto;
    white-space: nowrap;
    text-transform: uppercase;
    box-shadow: 0px 2px 3px #e8e8e8;
}

.scrollmenu a{
    display: inline-block;
    color: $grey;
    text-align: center;
    padding: 14px;
    text-decoration: none;
}

.scrollmenu a.active {
  color: $blue-green;
  // border-bottom: 2px solid $blue-green;
}

.scrollmenu a:hover{
    color: $blue-green;
}

.container-fluid>.navbar-collapse, .container-fluid>.navbar-header, .container>.navbar-collapse, .container>.navbar-header {
    margin-right: 0;
    margin-left: 0;
}

.indexcity {
  z-index: 9999;
}

.right {
    right: 170px !important;
}

@media only screen and (max-width: 767px) {
    .navbar-brand {
      display: none;
    }
}
