input[type=checkbox] {
	visibility: hidden;
}

input[type=radio] {
    display: none;
}

input:checked + label div {
  border:1px solid $blue-green;
}

input.valid  {
    border-bottom: 2px solid $blue-green;
}

input.invalid  {
    border-bottom: 2px solid $red-bg;
}

.code-submit {
  text-align: center;
}

.code-input {
  outline: none;
  background: #FFFFFF;
box-shadow: 0 2px 4px 0 rgba(0,0,0,0.11), inset 0 1px 3px 0 rgba(0,0,0,0.50);
border-radius: 8px;
width: 64px;
    height: 64px;
    font-size: 24px;
    text-align: center;
}

.forma-solicitud .panel {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.checkboxForm {
  width: 67%;
  margin: 51px auto;
  position: relative;
  label {
  	cursor: pointer;
  	position: absolute;
  	width: 25px;
  	height: 25px;
  	top: 0;
    left: 0;
  	background: #fff;
  	border:1px solid $blue-green;
    &:after {
    	opacity: 0.2;
      content: '';
      position: absolute;
      width: 13px;
      height: 7px;
      background: transparent;
      top: 7px;
      left: 6px;
      border: 2px solid $blue-green;
      border-top: none;
      border-right: none;
      transform: rotate(-45deg);
    }
  }
}

/**
 * Create the hover event of the tick
 */
.checkboxForm label:hover::after {
	opacity: 0.5;
}

/**
 * Create the checkbox state for the tick
 */
.checkboxForm input[type=checkbox]:checked + label:after {
	opacity: 1;
}

#detalles-solicitud .form-group {
    margin-top: 0;
}
